.sidebar-label-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  text-transform: capitalize;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.sidebar-label-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee;
  border-radius: 50%;
}

.sidebar-label-container:hover input ~ .checkmark {
  background-color: #ccc;
}

.sidebar-label-container input:checked ~ .checkmark {
  background-color: #432ee2;
}

.checkmark::after {
  content: "";
  position: absolute;
  display: none;
  top: 6.4px;
  left: 6.4px;
  width: 7px;
  height: 7px;
  background-color: white;
  border-radius: 50%;
}

.sidebar-label-container input:checked ~ .checkmark::after {
  display: block;
}
